import React, { useState, useEffect } from 'react'
import { useCookies } from 'react-cookie'
import Helmet from 'react-helmet'
import './reset.css'
import { PropTypes } from 'prop-types'
import Header from './header/header'
import Footer from './footer/footer'
import Intro from './intro/intro'

const Layout = ({ children }) => {
	const cookieConfig = {
		name: 'intro',
		path: '/',
		timeout: 3600,
		sameSite: 'lax',
	}
	const bodyVisibilityTimeout = 500
	const [playIntro, setPlayIntro] = useState(false)
	const [bodyVisibility, setBodyVisibility] = useState(false)
	const [cookies, setCookie] = useCookies([cookieConfig.name])
	const unmountIntro = () => {
		setPlayIntro(false)
		setBodyVisibility(true)
	}

	useEffect(() => {
		if (!cookies[cookieConfig.name]) {
			setPlayIntro(true)
			setCookie(cookieConfig.name, true, {
				path: cookieConfig.path,
				maxAge: cookieConfig.timeout,
				sameSite: cookieConfig.sameSite,
			})
			setTimeout(() => {
				setBodyVisibility(true)
			}, bodyVisibilityTimeout)
		} else {
			setBodyVisibility(true)
		}
	}, [
		cookieConfig.name,
		cookieConfig.path,
		cookieConfig.sameSite,
		cookieConfig.timeout,
		cookies,
		setCookie,
	])

	return (
		<>
			<Helmet>
				<title>James Maclennan - Front End Web Developer</title>
				<link
					href="https://fonts.googleapis.com/css?family=Roboto|Lato"
					rel="stylesheet"
				/>
			</Helmet>
			{playIntro ? <Intro unmountIntro={unmountIntro} /> : null}
			<div style={{ opacity: bodyVisibility ? 1 : 0 }}>
				<Header />
				<main>{children}</main>
				<Footer />
			</div>
		</>
	)
}

export default Layout

Layout.defaultProps = {
	children: '<div></div>',
}

Layout.propTypes = {
	children: PropTypes.node,
}
